const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;
const { RichText } = wp.editor;

import { ReactComponent as Logo } from "../bv-logo.svg";
import logoWhiteURL from "../bv-logo-white.svg";

registerBlockType("podkit/sample", {
    title: __("Sample", "podkit"),
    icon: { src: Logo },
    category: "podkit",
    attributes: {
      epsiodeTitle: {
        type: "string",
        source: "html",
        selector: ".podkit-title"
      }
    },
  
    edit: props => {
      // Props parameter holds all the info.
      console.info(props);
  
      // Lift info from props and populate various constants.
      const {
        attributes: { epsiodeTitle },
        className,
        setAttributes
      } = props;
  
      const onChangeEpisodeTitle = newEpisodeTitle => {
        setAttributes({ epsiodeTitle: newEpisodeTitle});
      };
  
      return (
        <button type="button" class="btn btn-primary"> 
        <RichText 
        placeholder={__("Button title", "podkit")}
        value={epsiodeTitle}
        onChange={onChangeEpisodeTitle}
        />
      </button>
      );
    },
    save: props => {
      const {
        attributes: { epsiodeTitle }
      } = props;
  
      return (
        <button type="button" class="btn btn-primary podkit-title"> 
        <RichText.Content value={epsiodeTitle}
        />
      </button>
    
      );
    }
  });
  